export default {
  name: 'Dialog',
  data () {
    return {
      search: ''
    }
  },
  computed: {
    /** —Ñ–ª–∞–≥ –∞–∫—Ç–∏–≤–Ω–æ—Å—Ç–∏ –¥–∏–∞–ª–æ–≥–æ–≤–æ–≥–æ –æ–∫–Ω–∞ */
    searchDialog () {
      return this.$store.state.dialog
    }
  },
  methods: {
    /** –∑–∞–∫—Ä—ã—Ç—å –≤—Å–ø–ª—ã–≤–∞—é—â–∏–π –¥–∏–∞–ª–æ–≥ */
    CloseDialog () {
      this.search = ''
      this.$store.dispatch('SetDialog', false)
    },
    /** –∑–∞–ø—É—Å—Ç–∏—Ç—å –ø–æ–∏—Å–∫ –∫–æ—Ä–∞–±–ª–µ–π –ø–æ –∏–º–µ–Ω–∏ –∏ –∑–∞–∫—Ä—ã—Ç—å –¥–∏–∞–ª–æ–≥ */
    SearchStarShips () {
      this.search && this.$store.dispatch('SearchStarShips', this.search)
      this.$router.push(`/starships/${this.search}`)
      this.CloseDialog()
    },
    /** –æ—Ç—Å–ª–µ–∂–∏–≤–∞–µ–º –Ω–∞–∂–∞—Ç–∏–µ –Ω–∞ enter –≤–æ –≤—Ä–µ–º—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è –ø–æ–ª—è –ø–æ–∏—Å–∫–∞ */
    PostMessageKeydown (e) {
      if (e.keyCode === 13 || e.which === 13) {
        e.preventDefault()
        this.SearchStarShips()
      }
    }
  }
}



