export default {
  name: 'Header',
  data () {
    return {
      search: '',
      searchDialog: false
    }
  },
  computed: {
    /** –ø—Ä–∏–∑–Ω–∞–∫ –æ–±—Ä–∞–±–æ—Ç–∫–∏ –¥–∞–Ω–Ω—ã—Ö –∏ –∑–∞–ø—É—Å–∫ —Å–ø–∏–Ω–Ω–µ—Ä–∞ */
    loading () {
      return this.$store.state.loading
    }
  },
  methods: {
    /** –æ—Ç–∫—Ä—ã—Ç—å –¥–∏–∞–ª–æ–≥–æ–≤–æ–µ –æ–∫–Ω–æ —Å –ø–æ–ª–µ–º –ø–æ–∏—Å–∫–∞ –∫–æ—Ä–∞–±–ª–µ–π */
    SearchStarShips () {
      this.$store.dispatch('SetDialog', true)
    },
    /** –ø–µ—Ä–µ–π—Ç–∏ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É —Å–ø–∏—Å–∫–∞ –∫–æ—Ä–∞–±–ª–µ–π */
    GetStarShipsList () {
      this.$store.dispatch('SearchStarShips', null)
      this.$router.push('/starships/null')
    }
  }
}



