export default {
  name: 'StarShipDetails',
  data () {
    return {
      details: [],
      headers: [
        {
          text: '–ù–∞–∏–º–µ–Ω–æ–≤–∞–Ω–∏–µ',
          align: 'left',
          value: 'name',
          sortable: false
        },
        {
          text: '–ó–Ω–∞—á–µ–Ω–∏–µ',
          align: 'right',
          value: 'value',
          sortable: false
        }
      ]
    }
  },
  computed: {
    /** –ø–æ–ª—É—á–∏—Ç—å –¥–µ—Ç–∞–ª–∏ –∫–æ—Ä–∞–±–ª—è */
    starShipsDetails () {
      this.details = this.$store.state.starShipsDetails
        ? this.AddDetailsItems(this.$store.state.starShipsDetails)
        : []
      return this.$store.state.starShipsDetails
    }
  },
  methods: {
    /** –ø—Ä–µ–æ–±—Ä–∞–∑—É–µ–º –æ–±—ä–µ–∫—Ç –≤ –º–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤ –¥–ª—è date-tables */
    AddDetailsItems (obj) {
      let items = []
      for (let key in obj) {
        let value = null
        if (Array.isArray(obj[key]) || key === 'url') {
          continue
        } else {
          value = obj[key]
        }
        items.push({name: key, value: value})
      }
      return items
    }
  }
}



