import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

const baseURL = 'https://swapi.co/api/'

export default new Vuex.Store({
  state: {
    loading: false,
    dialog: false,
    count: 0,
    starShipsList: [],
    starShipsDetails: null,
    nextPage: null,
    prevPage: null
  },
  actions: {
    /**
     * –æ—Ç–∫—Ä—ã—Ç—å –¥–∏–∞–ª–æ–≥–æ–≤–æ–µ –æ–∫–Ω–æ —Å –ø–æ–∏—Å–∫–æ–º
     * @param payload {boolean} –æ—Ç–∫—Ä—ã—Ç—å –∏–ª–∏ –∑–∞–∫—Ä—ã—Ç—å –æ–∫–Ω–æ
     */
    SetDialog ({commit}, payload) {
      commit('SetField', { name: 'dialog', value: payload })
    },
    /**
     * –ø–æ–∏—Å–∫ –∫–æ—Ä–∞–±–ª–µ–π –ø–æ –∏–º–µ–Ω–∏
     * @param payload {string|null} –ø–æ–∏—Å–∫–æ–≤–∞—è —Å—Ç—Ä–æ–∫–∞ –∏–ª–∏ null
     */
    SearchStarShips ({commit, rootGetters}, payload) {
      commit('SetField', { name: 'loading', value: true })
      const url = payload ? `starships/?search=${payload}` : 'starships/'
      rootGetters.ax.get(url)
        .then((response) => {
          commit('SetField', { name: 'nextPage', value: response.data.next })
          commit('SetField', { name: 'prevPage', value: response.data.previous })
          commit('SetField', { name: 'count', value: response.data.count })
          commit('SetField', { name: 'starShipsList', value: response.data.results })
        })
        .then(() => {
          commit('SetField', { name: 'loading', value: false })
        })
        .catch((err) => {
          commit('SetField', { name: 'loading', value: false })
          console.log(err)
        })
    },
    /**
     * –ü–æ–ª—É—á–∏—Ç—å –¥–µ—Ç–∞–ª–∏ –ø–æ –≤—ã–±—Ä–∞–Ω–Ω–æ–º—É –∫–æ—Ä–∞–±–ª—é
     * @param payload {string} url –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ –∫–æ—Ä–∞–±–ª—è
     */
    GetStarShipDetails ({commit, rootGetters}, payload) {
      commit('SetField', { name: 'loading', value: true })
      commit('SetField', { name: 'starShipsDetails', value: null })
      rootGetters.ax.get(payload)
        .then((response) => {
          commit('SetField', { name: 'starShipsDetails', value: response.data })
        })
        .then(() => {
          commit('SetField', { name: 'loading', value: false })
        })
        .catch((err) => {
          commit('SetField', { name: 'loading', value: false })
          console.log(err)
        })
    },
    /**
     * –ø–æ—Å—Ç—Ä–∞–Ω–∏—á–Ω–∞—è –Ω–∞–≤–∏–≥–∞—Ü–∏—è –¥–ª—è —Å–ø–∏—Å–∫–∞ –∫–æ—Ä–∞–±–ª–µ–π
     * @param payload {string} url –Ω–∞ —É–∫–∞–∑–∞–Ω–Ω—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É
     */
    GetStarShipsPage ({commit, rootGetters}, payload) {
      commit('SetField', { name: 'loading', value: true })
      rootGetters.ax.get(payload)
        .then((response) => {
          commit('SetField', { name: 'nextPage', value: response.data.next })
          commit('SetField', { name: 'prevPage', value: response.data.previous })
          commit('SetField', { name: 'count', value: response.data.count })
          commit('SetField', { name: 'starShipsList', value: response.data.results })
        })
        .then(() => {
          commit('SetField', { name: 'loading', value: false })
        })
        .catch((err) => {
          commit('SetField', { name: 'loading', value: false })
          console.log(err)
        })
    }
  },
  mutations: {
    /**
     * —É–Ω–∏–≤–µ—Ä—Å–∞–ª—å–Ω–∞—è –º—É—Ç–∞—Ü–∏—è –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è —Å–≤–æ–π—Å—Ç–≤ —Å—Ç–æ—Ä–∞
     * @param payload {Object} –æ–±—ä–µ–∫—Ç —Å –∏–º–µ–Ω–µ–º –∏ –∑–Ω–∞—á–µ–Ω–∏–µ–º –ø–æ–ª—è —Å—Ç–æ—Ä–∞
     */
    SetField (state, payload) {
      state[payload.name] = payload.value
    }
  },
  getters: {
    /**
     * –≥–µ—Ç—Ç–µ—Ä –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å –∑–∞–ø—Ä–æ—Å–∞–º–∏ –∫ –±—ç–∫–µ–Ω–¥—É
     * @returns {AxiosInstance}
     */
    ax () {
      return axios.create({
        baseURL,
        timeout: 60000,
        headers: {'Content-Type': 'application/json'}
      })
    }
  }
})



